import React from "react"
import Header from "./header/header"
import Footer from "./footer/footer"

// Add Case Study Header and a varible to render or not render the header

export default function Layout({name, children}) {
    let cn = "main" + " " + name
    return (
        <body>
            <Header />
            <main className={cn} role="main"> 
                {children}
            </main>
            <Footer />
        </body>
    )
}