import React from "react";
import Logo from "./logo";
import { Link, graphql, useStaticQuery } from "gatsby";
import "../../styles/header.scss";

export default function Header() {
  const data = useStaticQuery(graphql`
    query PdfQuery {
      allFile(filter: { extension: { eq: "pdf" } }) {
        edges {
          node {
            publicURL
          }
        }
      }
    }
  `);

  //   console.log(`data`, data.allFile.edges[0].node.publicURL);

  const pdfPath = data.allFile.edges[0].node.publicURL;

  const getPageName = () => {
    let page = window.location.pathname;

    if (page === "/") {
      return "Home";
    } else if (page === "/about/") {
      return "About Me";
    } else if (page.includes("/projects/")) {
      return "Project";
    }
  };

  // if the page url includes project display a different header

  const links = [
    { name: "About Me", link: "/about/" },
    { name: "Home", link: "/" },
    { name: "Resume", link: "" },
    // {name: 'Fun', link: '/fun'},
  ];

  function RenderList() {
    const pageName = getPageName();
    return (
      <ul className="menu" aria-label="Menu of links">
        {links.map(function (page) {
          if (page.name === pageName) {
            // If this is home or about, skip from render
            return;
          } else if (pageName === "Project" && page.name === "Home") {
            // if this is a project page remove the home link
            return;
          } else if (page.name !== "Resume") {
            // if anything but the resume use the Gatsby Link item
            return (
              <li key={page.name} id="nav-link-list-item">
                <Link to={page.link}>{page.name}</Link>
              </li>
            );
          }
          // default return
          return (
            <li key="resume" id="nav-link-list-item">
              <a href={pdfPath} target="_blank" type="application/pdf">{page.name}</a>
            </li>
          );
        })}
      </ul>
    );
  }

  return (
    <header className="main-header">
      <Logo />
      <nav className="header-navigation">
        <RenderList />
      </nav>
    </header>
  );
}
