import React from "react"
import { Link } from "gatsby"
import logo from "../../images/main-icon/main-icon.png"

export default function Logo() {
    return (
        <Link to='/' className="header-logo" id="header-logo" aria-label="Logo and Link for Sabrina Settle's portfolio">
                <img id="header-logo-image" src={logo} alt="Image Logo" />
                <span className="logo-name"></span>
        </Link>
    )
}