import React, { useState } from "react";

export default function EthosStatement() {
  const [displayEthos, setDisplayEthos] = useState(false);

  function renderEthos() {
    setDisplayEthos(!displayEthos);
  }

  let active = displayEthos
    ? "ethos-statement-toggle active"
    : "ethos-statement-toggle";
  return (
    <div id="ethos-container">
      <button className={active} onClick={renderEthos}>
        <div
          id="ethos-toggle-text"
          aria-label="text that indicts button for seeing a ethos statement"
        >
          Ethos Statement
        </div>
        <div id="circle-icon-ethos-statement" aria-label="A simple circle" />
      </button>
      <div id="ethos-statement">
        {displayEthos ? (
          <p>
            My personal ethos when it comes to design can be encapsulated
            by the quote by polymath, textile and print designer William Morris
            - “Have nothing in your houses that you do not know to be useful or
            believe to be beautiful”. I strive to design software which is useful, beautiful, and worth wandering.
          </p>
        ) : null}
      </div>
    </div>
  );
}
